.table-top{
  display: flex;align-items: center;justify-content: space-between;height: 56px;flex: 0 0 56px;position: relative;
  > .d-flex{flex: 1;}
}

.table-title{
  font-size: 18px; font-weight: 700;white-space: nowrap;text-overflow: ellipsis;overflow: hidden;padding-right: 1.5rem;
  span{ font-weight: 400; margin-left: 5px;}
}

.table-sub-title{
  font-weight: 700;margin-bottom: .75rem;
}

.table-function{ display: flex;align-items: center;gap: .5rem; }

.table-body{
  flex-grow: 1;overflow-y: auto;max-height: 100%;
}

.table{
  width: 100%;border-bottom: 1px solid #2d2e30;color: $GRAY_LIGHT_COLOR;
  th{font-weight: 400;}
  thead{
    th{color: #9B9FA4;padding: 0 .5rem;}
  }
  tbody{
    th,
    td{
      min-height: 100px; padding: .75rem .5rem;border-top: 1px solid #2d2e30;
    }
  }
  .selected{
    th,
    td{background: #3f4246;}
  }
  .muted{opacity: .4;}
  .modal &{
    th,
    td{border-color: #444;}
  }
}

.table-thead-fixed{
  thead {
    display:table;
    table-layout:fixed;
    width: calc( 100% - 6px );
    th{padding: .5rem;}
  }
  tbody {
    display:block;
    max-height:40vh;
    overflow-y:scroll;
  }
  tbody tr {
    display:table;
    table-layout:fixed;
    width:100%;
    th,
    td{padding: .5rem;}
  }
}

.tab-wrap{
  overflow-x: auto;
  .nav{
    display: inline-flex;
    .nav-link{
      padding: 0 1rem;display: flex;align-items: center;justify-content: center;height: 56px;
      &.active{color: $PRIMARY_COLOR;border-color: $PRIMARY_COLOR; font-weight: bold;font-weight: 700;}
    }
  }
}

.tab-content{
  padding-bottom: 1rem;overflow: hidden;
  > .tab-pane{display: none;}
  > .active{display: block;}
}

.card-sort{
  display: inline-flex;align-items: center;
  .form-check-label{white-space: nowrap;}
}

.search-form{
  background: transparent; border: 0; border-radius: 24px; position: relative; height: 100%;min-width: 20px;
  .form-control{ padding: 12px 3rem 12px 1rem;width: 180px;background: #33363A;display: none;border-color: #3e4042;}
  &.show{
    .form-control{display: block;}
  }
  .search-btn{ position: absolute; top: 50%; right: 15px; transform: translateY(-50%);}
}

// login
#login-wrap{height: 100%;}
.login{
  height: 100%;
  #container{ height: 100%; padding-top: 0;}
}
.login-content{
  display: flex; flex-flow: column; width: 100%; height: 100%;  justify-content: center; margin-top: -2%;
  // margin-top: 9.5vh;
  .login-header{
    text-align: center;
    @media(orientation: landscape) and (max-height: 640px){
      img{height: 50px;}
    }
  }
  .login-body{
    max-width: 1000px; width:100%; margin: 60px auto 0; padding: 60px 0 88px 0; border-radius: 16px; background-color: #151515;
    .login-form__title{
      font-size: 34px; font-weight: bold; line-height: 1; text-align: center; margin-bottom: 32px;
    }
    @media(orientation: landscape) and (max-height: 960px){
      margin: 2rem auto 0;padding: 2rem 0;
      .login-form__title{
        display: none;
      }
    }
  }
  .login-form{
    max-width: 360px; margin: 0 auto;
    .form-control{border-radius: 4px;}
    .login-form__box{
      margin-bottom: 25px;
      label{margin-bottom: 8px; font-size: 16px; font-weight: bold; line-height: normal;}
    }
    .btn-group{
      margin-top: 40px;
    }
    .btn{
      width: 100%; height: 48px;  border-radius: 4px; background-color: $PRIMARY_COLOR;
    }
  }
}

.setting-update{
  width: 100%; position: fixed; left: 0; bottom: 0; padding: 1rem 0; background-color: $PRIMARY_COLOR;
  > .container{ display:flex; align-items: center; justify-content: space-between ;}
  span{ font-size: 16px; color: #dfdfdf; line-height: 1.5; font-weight: bold; margin-left: 24px;}
}

.operation-tab{
  position: relative;
  &::after{content: '';position: absolute;bottom: 0;left: 0;height: 1px;width: 100%;background: #2d2e30;}
  .nav-tabs{height: 48px;display: flex;align-items: center;}
  .nav-link{
    font-size: 16px; line-height: 46px;color: #757575; font-weight: 700; margin-right: 1rem;position: relative;
    &:last-child{margin-right: 0;}
    &.active{
      color: #fff;
      &::before{content: '';width: 100%;height: 2px;background: #fff;position: absolute;left: 0;bottom:0;}
    }
  }
}

.group-emergency-head{ padding: 0 17px; border-bottom: 1px solid #33363A;}
.group-emergency-body{
  padding: 0 17px; flex-grow: 1; overflow: auto; height: 400px;
  &::-webkit-scrollbar { width: 6px; }
  &::-webkit-scrollbar-thumb { height: 30%; background: #676C72; border-radius: 9px; }
  &::-webkit-scrollbar-track { background: #000; }
}

.date-search-wrap{
  width:100%; display: flex; align-items: center; margin-bottom: 24px;
  .inp-calender{
    display: flex; align-items: center; background-color: #33363A; border: #3F4246; border-radius: 24px; position: relative; height: 48px;
    input{
      width:100%; background: transparent; border: 0; color: #fff; padding-left: 23px;
    }
    .icon-calendar{
      position: absolute; top: 50%; transform: translateY(-50%); right: 23px;
    }
  }
  .dash{ margin: 0 8px;}
  .date-search{margin-left: 14px;}
}

.message-box{
  padding: 0 1.5rem 1.5rem;
}

.message-date{
  position: relative;text-align: center; margin: 2rem auto;
  &::before{content: '';width: 100%;height: 1px;background: #33363A;position: absolute;left: 0;top: 50%;transform: translateY(-50%);}
  span{
    background-color: #111214; padding: 0 1rem;display: inline-block;margin: 0 auto;
    border-radius: 40px; color: $GRAY_LIGHT_COLOR; line-height: 26px;position: relative;
  }
  &:first-of-type{margin-top: 0;}
}

.message-item{
  padding: 1rem 0;width: 100%;
  .user{margin-bottom: 8px;font-weight: 700;}
  .message{
    display: flex;align-items: flex-end;
    .cont{
      background-color: #202020;border-radius: 4px;padding: 1rem;word-break: break-all;display: flex;align-items: center;max-width: 70%;
      a{color: $PRIMARY_COLOR;text-decoration: underline;}
      [class^=icon-]{margin-right: 8px;}
    }
    .time{font-size: 12px;margin-left: 8px;white-space: nowrap;color: $GRAY_LIGHT_COLOR;}
  }
  &.my{
    .message{display: flex;flex-direction:row-reverse;}
    .time{margin-left: 0; margin-right: .5rem;}
    .cont{background-color: #54595F;}
  }
}

.message-input{
  background-color: #54595f;height: 48px;margin: 0 0 -1rem;
  .form-control{
    color: #fff;background: #54595f;border: 0;height: 100%;padding-left: 0;padding-right: 1rem;
    &::placeholder{color: #9b9fa4;}
  }
  .message-input-btn{width:48px; height:48px;background-color: $PRIMARY_COLOR;}
}

/* Group */
.group-add-top{
  margin-bottom: 1rem;color: $GRAY_LIGHT_COLOR;
  strong{display: block;font-size: 18px;font-weight: 700;}
}

/* Group Detail */
.call-function{
  background: #2A2C2E;border: 1px solid #3f4143;border-radius: .5rem;margin-bottom: 1.5rem;margin-top: 1.5rem;
  display: flex;align-items: center;justify-content: center; text-align: center;padding: 1.5rem 0;
  button{margin: 0 1rem;
    &[disabled]{opacity: .35;}
  }
  span{font-size: 14px;display: block;margin-top: 6px;}
  i{width: 32px;height: 32px;}
}

/* log */
.log-item{margin-top: 2rem;
  .table{
    border-top: 1px solid $BORDER_COLOR;border-bottom: 1px solid $BORDER_COLOR;
    th,
    td{border: 0;}
  }
}
.filter-wrap{
  border-top: 1px solid #444;border-bottom: 1px solid #444;padding: .5rem;margin-top: .5rem;display: grid;grid-template-columns: 1fr 1fr;
  .form-check{margin: .5rem 0}
}

/* Settings */
.logout{
  text-align: right;border-top: 1px solid rgba(0,0,0,0.12);padding: 1rem;
  a{color: $GRAY_LIGHT_COLOR;}
  i{margin-left: 4px;}
}

/* Card Menu */
.card-menu{
  .card-menu-top{
    margin-bottom: 2rem;margin-top: .5rem;display: flex;align-items: center;justify-content: center;text-align: center;
    button{
      margin: 0 1rem;
      &[disabled]{opacity: .35;}
      i{width: 32px;height: 32px;}
    }
    span{font-size: 14px;display: block;margin-top: 6px;}
  }
  .card-menu-body{
    .set{
      border-top: 1px solid rgba(255,255,255,.1);padding: .25rem 0;
      li{
        padding: .75rem 1.5rem;
        a{display: inline-flex;align-items: center;vertical-align: top;}
      }
      i{margin-right: .5rem;}
    }

  }
}